* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.countries-item {
  list-style: none;
  display: flex;
  align-items: center;
  font-size: 10px;
  gap: 10px;
}

.country-wrapper {
  display: flex;
  align-items: center;
  font-size: 14px;
  gap: 10px;
}

.country-img {
  width: 30px;
  height: 20px;
}